import React from 'react';
import styles from './subline.css';
import classnames from 'classnames';

type Props = {
  small?: boolean;
  className?: string;
  children: string;
};

export default function Subline({ className, children, small }: Props) {
  return (
    <span
      className={classnames(className, styles.subline, small && styles.small)}
    >
      {children}
    </span>
  );
}
